export default {
  state: {
    bots: null,
  },

  mutations: {
    setBots(state, bots) {
      state.bots = bots
    },
    removeBot(state, botId) {
      state.bots = state.bots.filter(bot => bot.id !== botId)
    },
  },

  actions: {
    fetchBots({commit}) {
      const bots = [
        {
          id: 9457836,
          createdAt: '2020-05-16T15:07:25.507Z',
          name: 'ZDownBot',
          agent: null,
          isEnabled: true,
        },
        {
          id: 78904123,
          createdAt: '2020-05-16T23:07:25.507Z',
          name: 'AUpBot',
          agent: null,
          isEnabled: false,
        },
      ]
      // TODO: Request bots
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          commit('setBots', bots)
          resolve(bots)
        }, 2000)
      })
    },

    deleteBot({commit}, botId) {
      // TODO: Delete bot
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          commit('removeBot', botId)
          resolve()
        }, 1500)
      })
    },
  },
}
